.docs_tree {
  width: 100%;
}

.docs_tree p.tree_item {
  margin-left: 10px;

  position: relative;

  width: max-content;

  display: flex;
  align-items: center;
  justify-content: center;

  z-index: 1;

  cursor: pointer;
}

.docs_tree p.tree_item::after {
  content: "";

  position: absolute;

  width: 100%;
  height: 100%;

  background: var(--main-button-background-active);
  border-radius: 7px;

  z-index: -1;

  padding: 4px 8px;

  opacity: 0;

  transition: 0.15s all;
}

.docs_tree p.tree_item.active::after {
  opacity: 1;
}

.docs_tree p.tree_item:not(.active):hover::after {
  opacity: 0.25;
}

.docs_article {
  padding-top: 40px;

  margin-bottom: 40px;
}

.docs_article blockquote {
  width: 100%;
  margin: 0;

  box-sizing: border-box;

  /* padding-left: 20px; */

  padding: 10px 0px 10px 20px;
  position: relative;

  background: var(--main-button-background-active);
}

.docs_article blockquote::before {
  content: "";

  position: absolute;
  left: 0;
  top: 0;
  width: 3px;
  height: 100%;

  background: var(--main-quote-bg);
}


.docs_row{
  justify-content: space-around;
}


@media (max-width: 1440px){
  .system_wrapper{
    width: 1140px;
  }
  .system_wrapper > div {
    width: auto;
  }
} 

@media (max-width: 1140px){
  .system_wrapper{
    width: 768px;
  }
  .system_wrapper > div {
    width: auto;
  }

  .system_wrapper .docs_article{
    width: 100%;
  }

  .system_wrapper .docs_tree_wrap{
    display: none;
  }

  
} 