body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --main-button-background: #ced4f7;
  --main-button-color: #242630;
  --main-button-background-hover: #ced4f7;
  --main-button-background-active: #ced4f7;
  --main-quote-bg: #b2b8d8;
  --menu-bg: #686bc73b;
  --footer-bg: #1b1c20;
  --footer-header: #fff;
  --footer-link: #fff;
}

.system_mode-dark {
  --main-bg-color: red;
}

body {
  background: var(--main-bg-color);
  font-family: "Roboto", sans-serif;
}

.codearea {
  width: 100%;

  height: min-content;

  position: relative;
}

.codearea textarea {
  width: 100%;
  max-width: 100%;

  min-height: 48px;

  max-height: 350px;

  background: #f8f9fa;
  border: 1px solid #eef0f2;

  border-radius: 7px;

  resize: vertical;
}

.codearea button {
  padding: 10px;

  background: none;

  border-radius: 7px;

  position: absolute;
  top: 5px;
  right: 5px;

  margin: 0;

  overflow: visible;

  display: flex;
  align-items: center;
  justify-content: center;
}

.codearea button:hover {
  background: var(--main-button-background-active);

  transform: scale(1);
}

.codearea > button:active {
  transform: scale(0.95);
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: var(--main-button-background-active) var(--main-body-bg);
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 4px;
  margin-right: 2px;
}

*::-webkit-scrollbar-track {
  background: var(--main-body-bg);
}

*::-webkit-scrollbar-thumb {
  background-color: var(--main-button-background-active);
  border-radius: 12px;
  width: 4px;
}

textarea::-webkit-scrollbar {
  display: none;
}

.menu {
  height: 75px;
  min-height: auto;
}

.content {
  padding-top: 100px;
}

.footer {
  height: 200px;
  min-height: auto;
}

.footer_wrapper {
  background: var(--footer-bg);
  color: var(--footer-link);
}

.footer_wrapper {
  background: var(--footer-bg);
  color: var(--footer-link);
}

.footer_wrapper h1,
.footer_wrapper h2,
.footer_wrapper h3,
.footer_wrapper h3,
.footer_wrapper h5,
.footer_wrapper h6 {
  color: var(--footer-header);
}

button.navigation-button-menu,
button.navigation-button-menu:active,
button.navigation-button-menu:hover {
  background: none;
  transform: scale(1);

  padding: 5px 10px;
  border-radius: 7px;

  margin-left: 4px;
  margin-right: 4px;

  font-size: 16px;

  font-family: "Roboto";
  font-weight: 500;

  color: #1b1c20;

  transition: 0.15s all;
}

button.navigation-button-menu:hover {
  background: #b5b8f1;
}

button.navigation-button-menu.active {
  background: #585dcd;
  color: #fff;
}

.burger {
  display: none;
}

svg.not_found {
  animation: bounceUp 0.3s;
}

@keyframes bounceUp {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 1140px) {
  .menu.system_wrapper > div .system_row > .system_cell {
    width: auto;
  }
}
